.NodePicker {
    border: 1px solid #EEE;
}

.NodePicker > ul {
    max-height: 200px;
    max-width: 300px;
    overflow: auto;
}
.NodePicker ul {
    margin: 0;
    padding: 0;
    list-style-type: none;
    list-style-position: inside;
    font-family: monospace;
    font-size: 12px;
}
.NodePicker ul ul {
    padding: 0 0 0 8px;
}

.NodePicker li {
    margin: 0;
    padding: 0;
}

.NodePicker span {
    display: block;
    letter-spacing: 0px;
    white-space: nowrap;
}

.NodePicker .selected {
    background: #1CAAD9;
    color: #FFF;
}

.NodePicker__pick.active {
    background: #1CAAD9;
    color: #FFF;
}


