.material-picker {
    display: -ms-flexbox;
    display: flex;
}

.MaterialPicker__pick {
    color: #333;
    background: transparent;
    border: 0;
    border-radius: 3px;
}

.MaterialPicker__pick.active {
    background: #1CAAD9;
    color: #FFF;
}


