html,
body,
#root {
    margin: 0;
    padding: 0;
    font-family: sans-serif;
    font-size: 14px;
    height: 100%;
    width: 100%;
}

.input.--text {
    border: 1px solid #ccc;
    border-radius: 3px;
    padding: 2px 4px;
    font-size: 14px;
}

.input.--text:focus {
    border: 1px solid #1caad9;
    outline: 0;
}

.select {
    font-size: 14px;
}

.button {
    border: 0;
    background: #888;
    color: #fff;
    border-radius: 3px;
    padding: 8px 12px;
    text-transform: uppercase;
    cursor: pointer;
}

.button.--primary {
    background: #1caad9;
}

.button:disabled {
    pointer-events: none;
    opacity: 0.5;
}

.button.--icon {
    background: transparent;
    color: #333;
}

.button.--icon:hover {
    color: #1caad9;
}

select,
input[type="text"] {
    font-size: 16px;
}


